<template>
  <div>
    <Header></Header>
    <wenzi v-if="qqq" @childsay="tochild"></wenzi>
    <div class="conent">
      <ul>
        <li v-for="a in com">
          <div class="da">
            <div class="left"><img :src="a.userImg" alt=""></div>
            <div class="dazhong">
              <div class="zhong">
                <h3>{{a.userName}}</h3>
                <div class="zx">
                  <i class="iconfont icon-shijian"></i>
                  <span>{{a.createdAt}}</span>
                  <i class="iconfont icon-laba"></i>
                  <span>公开</span>
                </div>
              </div>
              <div class="right"><span>党员互动</span></div>
            </div>
          </div>
          <div class="neirong">{{a.content}}</div>
          <div class="di" @click="huii(a._id)">
            <i class="iconfont icon-tubiao15"></i>
            <span>回复</span>
          </div>
        </li>
      </ul>
      <img src="../assets/发布btn.png" id="fabuhui" @click="fabuhui" v-if="aas">
    </div>
  </div>
</template>

<script>
  import Header from '@/base/Header.vue'
  import wenzi from '@/base/wenzi.vue'

  export default {
    name: "Minzhu",
    components: {
      Header, wenzi
    },
    data() {
      return {
        qqq: false,
        aas: true,
        com:[]
      }
    },
    methods: {
      getdata(){
        this.$axios.post('carget').then(res=>{
          console.log(res);
          this.com=res.data.data
        })
      },
      fabuhui() {
        this.qqq = true
        this.aas = false
      },
      huii(val){
        this.$router.push({path: '/huifu',query:{newsId:val}})
      },
      tochild(somedata) {
        this.qqq=somedata;
        this.aas=true
      }
    },
    mounted(){
      this.getdata();
    }
  }

</script>

<style scoped>
  ul li {
    padding: 10px;
  }

  .da {
    display: flex;
  }

  .dazhong {
    display: flex;
    justify-content: space-between;
  }

  .zhong {
    padding: 0 25px 0 8px;
  }

  .left img {
    height: 40px;
    width: 40px;
    border-radius: 50%;
  }

  .di {
    text-align: right;
  }

  .right span {
    color: red;
    border-radius: 10px;
    border: 1px solid red;
    padding: 2px 6px;
    font-size: 10px;
  }

  .neirong {
    font-size: 18px;
  }

  .zx {
    font-size: 10px;
  }

  #fabuhui {
    position: fixed;
    right: 5px;
    bottom: 30px;
  }
</style>



